import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityAssignmentItemComponent } from './activity-assignment-item.component';
import { ActivityAssignmentItemStudentComponent } from './activity-assignment-item-student/activity-assignment-item-student.component';


const routes: Routes = [
  { path: '', component: ActivityAssignmentItemComponent,
    children: [
      { path: 'student/:studentid', component: ActivityAssignmentItemStudentComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityAssignmentItemRoutingModule { }
