import { Component, OnInit, ViewChild,TemplateRef, HostListener, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { MatDialog } from '@angular/material/dialog';

import { ActivityAssignmentItemComponent } from 'src/app/modules/admin/activity/activity-assignment/activity-assignment-item/activity-assignment-item.component';

import { ActivityAssignmentService } from 'src/app/modules/admin/activity/activity-assignment/activity-assignment.service';

import * as moment from 'moment';

@Component({
  selector: 'app-activity-assignment-item-student',
  templateUrl: './activity-assignment-item-student.component.html',
  styleUrls: ['./activity-assignment-item-student.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ActivityAssignmentItemStudentComponent implements OnInit {

  student:any = {}

  activity:any = {}

  @ViewChild("submitAssignmentRef", { static: false }) submitAssignmentRef: TemplateRef<any>;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    public dialog: MatDialog,
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private _activityHomeworkItemComponent: ActivityAssignmentItemComponent,
    private activityAssignmentService: ActivityAssignmentService
  ) { }

  goBack() {
    this._activityHomeworkItemComponent.matDrawer.close();
    this.activityAssignmentService.setAssignmentActivityStudentData(null)
    this._router.navigateByUrl('/activity/assignment/list/details/'+this.activity.id);
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  openSubmitModel() {

    this.dialog.open(this.submitAssignmentRef, {
      panelClass: 'material-dialog-medium'
    });

  }

  submitStudentActivityForm() {

    this.dialog.closeAll()

  }

  ngOnInit(): void {

    this.activityAssignmentService.assignmentactivitystudentdatacast.subscribe((res:any) => {
      if(res != null){
        this._activityHomeworkItemComponent.matDrawer.open();
        this.student = res.data
      }
    })

    this.activityAssignmentService.assignmentactivitydatacast.subscribe((res:any) => {
      if(res != null){
        this.activity = res
      }
    })

  }

}
