import { Component, Input, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Subscription, Subject } from 'rxjs';

import { MatDrawer } from '@angular/material/sidenav';

import { ActivityAssignmentService } from 'src/app/modules/admin/activity/activity-assignment/activity-assignment.service';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

import * as moment from 'moment';

@Component({
  selector: 'activity-assignment-item',
  templateUrl: './activity-assignment-item.component.html',
  styleUrls: ['./activity-assignment-item.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ActivityAssignmentItemComponent implements OnInit {

  _studentsList:any = [];
  fullStudentsList:any = [];

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  searchData:FormControl;

  search:string = '';
  apiSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any = 0;
  itemLimit: any = 5;

  allData: any;

  message: any;

  activity:any = {}


  constructor(
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private _changeDetectorRef: ChangeDetectorRef,
    private activityAssignmentService: ActivityAssignmentService,
    private sharedService:SharedService,
    private sessionService: SessionService,
    private modalsService: ModalsService
  ) {

    this.message = null;

    this._studentsList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

  }

  onBackdropClicked() {

    // Get the current activated route
    let route = this._activatedRoute;
    while ( route.firstChild )
    {
        route = route.firstChild;
    }

    // Go to the parent route
    this.activityAssignmentService.setAssignmentActivityStudentData(null)
    this._router.navigateByUrl('/activity/assignment/list/details/'+this.activity.id);
    this.matDrawer.close();

    // Mark for check
    this._changeDetectorRef.markForCheck();

  }

  get students$(): Observable<any>{
    return this._studentsList.asObservable();
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  getStatus(value){
    if(value == 'Handed In'){
      return 'orange'
    }
    else if(value == 'Corrected') {
      return 'green'
    }
    else {
      return 'red'
    }
  }

  goToStudent(event, student) {

    if(event) {

      // Get the current activated route
      let route = this._activatedRoute;
      while ( route.firstChild )
      {
          route = route.firstChild;
      }

      var id = this.sharedService.guid();

      this.activityAssignmentService.setAssignmentActivityStudentData({id: this.activity.id, studentid:student.id, data: student});
      this._router.navigate(['student/', student.id], {relativeTo: route});

      //Mark for check
      this._changeDetectorRef.markForCheck();

    }

  }


  ngOnInit(): void {

    let res = [
      {
        "id": 24,
        "name": "John Deo",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "dueDate": "2021-04-06T08:57:32.270Z",
        "status": 'Handed In',
        "comments": 2,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
      {
        "id": 56,
        "name": "Michael Clares",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "dueDate": "2021-04-06T08:57:32.270Z",
        "status": 'Corrected',
        "comments": 2,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      },
      {
        "id": 87,
        "name": "Thomos",
        "description": "some open program content",
        "startDate": "2021-03-06T08:57:32.270Z",
        "dueDate": "2021-04-06T08:57:32.270Z",
        "status": 'Not Submitted',
        "comments": 2,
        "active": true,
        "lastModified": "2021-03-06T08:57:32.270Z",
        "modifiedBy": 1
      }
    ];

    this.fullStudentsList = res;
    this.allData = res;
    this._studentsList.next(res);

    this.students$.pipe( filter(res => res != null),
    ).subscribe((res:any) => {

      this.totalItems = res.length;
      this.ItemStartIndex = 0;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

    })

    // Subscribe to search input field value changes
    this.searchData.valueChanges
    .pipe(
      startWith(''),
      map((val) => {

        let newData = this.fullStudentsList.filter(item => {
          for (let field in item) {
            if (item[field] === null || item[field] === undefined) {
              continue;
            }
            if (item[field].toString().toLowerCase().indexOf(val.toString().toLowerCase()) !== -1) {
              return item;
            }
          }
        })

        this._studentsList.next(newData);


      })
    )
    .subscribe();

    this.activityAssignmentService.assignmentactivitydatacast.subscribe((res:any) => {
      if(res != null) {
        this.activity = res
      }
    })

  }

}
